#skill,#foot,#writing,#contact{
  padding: 40px 0;
  position: relative;
}
.wrap{
  position: relative;
  left: 0;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.remove{
  width: 100%;
  left: 266px;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
/*me-info start*/
.open-me{
  opacity: 1!important;
  left: 0!important;
}
.me-info{
  position: fixed;
  left: -100%;
  top: 0;
  width: 266px;
  height: 100%;
  background: #000;
  color: #fff;
  padding: 30px 10px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  opacity: 0;
}
.me-info>.me>.me-pic{
  width: 160px;
  height: 160px;
  display: block;
  margin: 0 auto;
  /*overflow: hidden;*/
  border-radius: 50%;
  -webkit-box-shadow: 0 0 15px #f9e249;
          box-shadow: 0 0 15px #f9e249;
}
.me-pic .me-wrap{
  width: 160px;
  height: 160px;
  display: block;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  z-index: 1;
}
.me-pic .me-wrap img:hover{
  -webkit-transform: scale(2);
          transform: scale(2);
  -webkit-transition: .5s;
  transition: .5s;
}
.me-pic .me-wrap img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-transition: .5s;
  transition: .5s;
}
.me-info>.me>h3{
  font-size: 18px;
  text-align: center;
  padding: 10px 0;
  margin-bottom: 10px;
}
.me-txt{
  text-indent: 28px;
  line-height: 1.6;
  padding: 0 10px;
  margin-bottom: 30px;
}
.detail{
  padding-left: 10px;
  text-align: left;
  text-indent: 0!important;
  line-height: 1.6;
  text-shadow: 2px 2px 2px #f6b6ab;
  letter-spacing: 1px;
  font-size: 16px;
}
.detail>a{
  color: #d4d4d4;
}
.close{
  position: absolute;
  top: 10px;
  right: 10px;
  color: #d6d6d6;
  font-size: 20px;
  cursor: pointer;
}
.se-title{
  padding: 50px 0;
}
.se-title>h2,.se-title>span,.se-title>h4{
  text-align: center;
  letter-spacing: .1rem;
}
.se-title>h2{
  letter-spacing: .5rem;
  font-size: 18px
}
.se-title>span{
  display: block;
  width: 10%;
  height: 2px;
  background: #871a04;
  margin: 14px auto;
}
.se-title>h4{
  text-transform: uppercase;
  font-size: 16px;
}
.scroll{
  -webkit-animation-name: entrance;
  animation-name: entrance;
  -webkit-animation-duration: 1.6s;
  animation-duration: 1.6s;
  opacity: 1 !important;

  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}
@-webkit-keyframes bg{
0% {
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
}
100% {
      -webkit-transform: translate3d(-1200px,0,0);
      transform: translate3d(-1200px,0,0);
}
}
@keyframes bg{
0% {
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
}
100% {
      -webkit-transform: translate3d(-1200px,0,0);
      transform: translate3d(-1200px,0,0);
}
}
@-webkit-keyframes entrance{
0% {
      -webkit-transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);
              transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);
      opacity: .2;
}
30% {
      -webkit-transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);
              transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);
      opacity: 1;
}
45% {
      -webkit-transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);
              transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
60% {
      -webkit-transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);
              transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
75% {
      -webkit-transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);
              transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
90% {
      -webkit-transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);
              transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);
      opacity: 1;
}
100% {
      -webkit-transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);
              transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);
      opacity: 1;
}
}
@keyframes entrance{
0% {
      -webkit-transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);
              transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);
      opacity: .2;
}
30% {
      -webkit-transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);
              transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);
      opacity: 1;
}
45% {
      -webkit-transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);
              transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
60% {
      -webkit-transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);
              transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
75% {
      -webkit-transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);
              transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);
      opacity: 1;
}
90% {
      -webkit-transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);
              transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);
      opacity: 1;
}
100% {
      -webkit-transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);
              transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);
      opacity: 1;
}
}







































































































